.customerform
{
     height: 700px;
     overflow: scroll;
}
.customerform::-webkit-scrollbar{
    width: 0;
}
.container_details1{
    display: flex;
    padding: 23px 24px;
}
.form-left
{
    width: 50%;
    height:715px;
}
.form-center
{
    width: 50%;
    height: 715px;
    margin-left: 25px;
}

.form-right
{
    width: 30%;
    height: 715px;
    margin-left: 25px;
    margin-right: 10px;
}
.box_heading1{
    padding-top: 20px;
    align-items: center;
    margin-top: 0px;
    display: flex;
    margin-left: 24px;
    width: 130px;
    color: #566A7F;
    height: 24px;
    font-weight: bold;
    font-size: 18px;
}
